.result {
  @media (min-width: @width-tablet) {
    display: flex;
    align-items: flex-start;
  }

  @media (min-width: @width-desktop) {
    flex-direction: column;
  }
}

.result__list {
  .list-reset();
  display: flex;
  justify-content: center;
}

.result__item {
  position: relative;
  padding: 13px 1px 16px;
  width: 123px;
  border: 1px solid #cdcdcd;
  border-radius: 3px;
  text-align: center;

  &--weight {
    margin-right: 33px;

    @media (min-width: @width-tablet) {
      margin-right: 42px;
    }
  }

  @media (min-width: @width-tablet) {
    width: 160px;
    padding: 15px 0 18px;
  }
}

.result__item-value {
  font-size: 24px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 24px;

  @media (min-width: @width-tablet) {
    font-size: 30px;
    line-height: 37px;
    margin: 16px auto;
  }
}

.result__item-text {
  position: absolute;
  top: 45px;
  left: 13px;
  padding: 0 17px;
  font-family: @font-second;
  font-size: 12px;
  color: @color-text;
  text-transform: lowercase;
  line-height: 12px;
  width: 60px;
  text-align: center;
  background-color: @color-bg;

  @media (min-width: @width-tablet) {
    top: 63px;
    left: 19px;
    width: 128px;
    padding: 0;
    font-size: @fz-small;
    background-color: #ffffff;
  }

  @media (min-width: @width-desktop) {
    background-color: @color-bg;
  }
}

.result__item--weight .result__item-text {
  @media (min-width: @width-tablet) {
    width: 115px;
  }
}

.result__total-costs {
  text-transform: uppercase;
  font-size: @fz-small;
  text-align: center;
  line-height: 20px;
  padding-top: 20px;

  @media (min-width: @width-tablet) {
    font-size: 20px;
    line-height: 30px;
    width: 175px;
    text-align: right;
    margin: 0 0 0 auto;
    padding-top: 7px;
  }

  @media (min-width: @width-desktop) {
    display: flex;
    justify-content: space-between;
    width: 327px;
    text-align: left;
    margin: 0;
    padding-top: 55px;
  }
}
